

export class ProjectProps {
    name: string;
    description: string;
    svn_url: string;
    homepage: string;
    stargazers_count: string;
    languages_url: string;
    pushed_at: Number;

    constructor() {
        this.name = '';
        this.description = '';
        this.svn_url = '';
        this.homepage = '';
        this.stargazers_count = '';
        this.languages_url = '';
        this.pushed_at = Date.now();
    }
}