import React, { useState, useEffect, useCallback } from "react";
import axios from "axios";

const ProjectCard = ({ value }: any) => {
  const {
    name,
    description,
    svn_url,
    homepage,
    stargazers_count,
    languages_url,
    pushed_at,
  } = value;

  return (
    <div className="col-sm-12 col-md-12 col-lg-6 mt-4">
      <div className="card h-100 shadow p-3 bg-white rounded">
        <div className="card-body d-flex flex-column">
          <div className="card-title h3">{name} </div>
          <div className="card-text mb-2">{(description) ? description : null} </div>
          {svn_url ? <CardButtons svn_url={svn_url} homepage={homepage} /> : null}
          <hr />
          {languages_url ? (
            <Language languages_url={languages_url} repo_url={svn_url} />
          ) : null}
          {value ? (
            <Updated star_count={stargazers_count} repo_url={svn_url} pushed_at={pushed_at} />
          ) : null}
        </div>
      </div>
    </div>
  );
};

const CardButtons = ({ svn_url, homepage }: any) => {
  return (
    <div className="mt-auto">
      <a href={svn_url} target=" _blank" className="btn btn-outline-secondary mr-1" style={{ height: '44px' }}>
        <i className="icon-github align-middle" style={{ fontSize: "26px" }}></i>
        <span className="align-middle px-2" style={{ marginLeft: '0.5rem' }}>View on Github</span>
      </a>
      {homepage.length > 0 ?
        <a href={homepage} className="btn btn-outline-success mr-1" style={{ height: '44px' }} target=" _blank"    >
          <span className="align-middle px-2">Run</span>
        </a>
        : null
      }
    </div>
  )
};

const Language = ({ languages_url, repo_url }: any) => {
  const [data, setData] = useState([]);

  const handleRequest = useCallback(async () => {
    try {
      const response = await axios.get(languages_url);
      return setData(response.data);
    } catch (error: any) {
      console.error(error.message);
    }
  }, [languages_url]);

  useEffect(() => {
    handleRequest();
  }, [handleRequest]);

  const languages = [];
  let total_count = 0;
  for (let index in data) {
    languages.push(index);
    total_count += data[index];
  }
  // TODO: fix C# link, -> c%23
  return (
    <div className="pb-2">
      Languages:{" "}
      {languages.length
        ? languages.map((language, index) => (
          <a
            key={language + index}
            className="badge badge-light card-link"
            href={repo_url + `/search?l=${language}`}
            target=" _blank"
          >
            {language}:{" "}
            {Math.trunc((data[language] / total_count) * 1000) / 10} %
          </a>
        ))
        : "No code deployed."}
    </div>
  );
};

const Updated = ({ pushed_at }: any) => {
  const [updated_at, setUpdated_at] = useState("0 mints");

  const handleUpdatetime = useCallback(() => {
    const date = new Date(pushed_at);
    const nowdate = new Date();
    const diff = nowdate.getTime() - date.getTime();
    const hours = Math.trunc(diff / 1000 / 60 / 60);

    if (hours < 24) {
      if (hours < 1) return setUpdated_at("just now");
      let measurement = hours === 1 ? "hour" : "hours";
      return setUpdated_at(`${hours.toString()} ${measurement} ago`);
    } else {
      const time = new Intl.DateTimeFormat("en-US", { day: "numeric", month: "long", year: "numeric" }).format(date);
      return setUpdated_at(`on ${time}`);
    }
  }, [pushed_at]);

  useEffect(() => {
    handleUpdatetime();
  }, [handleUpdatetime]);

  return (
    <p className="card-text">
      <small className="text-muted">Updated {updated_at}</small>
    </p>
  );
};

export default ProjectCard;