import React, { FC } from 'react';
import { config } from '../config';

const Technologies: FC = () => {
    const heading = config.skills_section.title;
    return (
        <section className="colorlib-skills" data-section="skills">
            <div className="colorlib-narrow-content">
                <div className="row">
                    <div className="col-md-6 col-md-offset-3 col-md-pull-3">
                        <h2 className="colorlib-heading">{heading}</h2>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 d-flex flex-row flex-wrap justify-content-around testers">
                        {config.skills_section.technologies.map((technology, index) => (
                            <div className="technology text-center mx-auto px-2" key={index}>
                                <img className="technology-icon" src={technology.img_url} alt={technology.name} />
                                <h5 className="technology-header mx-auto">{technology.name}</h5>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Technologies
