import React, { useState, useEffect, useCallback } from "react";
import { config } from "../config";
import ProjectCard from "./ProjectCard";
import Skeleton from "react-loading-skeleton";
import ProjectCardCustom from "./ProjectCardCustom";
import axios from "axios";

import { ProjectProps } from '../models/ProjectProps';
import { ProjectCustomProps } from '../models/ProjectCustomProps';


const API = "https://api.github.com";
const username = "gitchaoslord";
const finalAPI = `${API}/users/${username}/repos?sort=updated&direction=desc`;
const reposToShow = config.projects_section.project_show_count;
const placeholderProjectsArray = new Array(reposToShow).fill(new ProjectProps);
const specificRepos = config.projects_section.show_projects;

const customRepos: ProjectCustomProps[] = [{
  name: "Inventory Management App",
  description: "Ionic/Angular web app developed for a custom android device, Sunmi L2, in order to help the company's staff quickly identify product info and assist in the 'Year-end Inventory' process.",
  svn_url: "",
  languages: ["HTML", "TypeScript", "JavaScript", "SCSS"],
  homepage: "",
  last_update: "on December 10, 2020",
  custom_link: "",
  custom_link_text: "",
},
{
  name: "Platinum Radiotunes mobile App",
  description: "Official App for the radio station Platinum Radiotunes.Currently only available in Greece.",
  svn_url: "",
  languages: ["HTML", "TypeScript", "JavaScript", "SCSS"],
  homepage: "",
  last_update: "on May 2, 2020",
  custom_link: "https://play.google.com/store/apps/details?id=com.platinum.radiotunes",
  custom_link_text: "View on Google Play",
}]

const Projects = () => {
  const [loading, setLoading] = useState<boolean>(true);
  const [projectsArray, setProjectsArray] = useState<ProjectProps[] | []>([]);

  const fetchRepos = useCallback(async () => {
    let reposList: ProjectProps[] = [];
    try {
      const response = await axios.get(finalAPI);
      // reposList = [...response.data.slice(0, reposToShow)]; 
      reposList = [...response.data];
      setProjectsArray(reposList.filter(obj => {
        if (specificRepos.includes(obj.name)) {
          return obj;
        }
      }));
      setLoading(false);
    } catch (error: any) {
      console.error(error?.message);
    }
  }, [finalAPI]);


  useEffect(() => {
    fetchRepos();
  }, [fetchRepos]);

  return (
    <section className="colorlib-projects" data-section="projects">
      <div className="colorlib-narrow-content">
        <h2 className="colorlib-heading">{config.projects_section.title}</h2>
        <div className="row">
          {!loading ? customRepos.map((project: ProjectCustomProps, index: number) => (
            <ProjectCardCustom
              key={`custom-project-card-${index}`}
              id={`custom-project-card-${index}`}
              value={project}
            />
          ))
            :
            (null)}
          {!loading
            ? projectsArray.map((project: ProjectProps, index: number) => (
              <ProjectCard
                key={`project-card-${index}`}
                id={`project-card-${index}`}
                value={project}
              />
            )) // You will be able to get the current iteration's index for the map method through its 2nd parameter
            : placeholderProjectsArray.map((currentElement: null, index: number) => (
              <div className="col-md-6" key={index}>
                <div className="card shadow p-3 mb-5 bg-white rounded">
                  <div className="card-body">
                    <div className="card-title h3">{<Skeleton />} </div>
                    <div className="card-text">
                      {<Skeleton count={2} />} </div>
                    <hr />
                    <div className="pb-2">
                      <Skeleton count={1} />
                    </div>
                    <Skeleton count={1} />
                  </div>
                </div>
              </div>
            ))}
        </div>
      </div>
    </section>
  );
};

export default Projects;