import React, { useState } from 'react';

interface IThemeContext {
  isLightTheme: boolean;
  light: { syntax: string, ui: string, bg: string },
  dark: { syntax: string, ui: string, bg: string },
  toggleTheme?: () => void;
}

const defaultState: IThemeContext = {
  isLightTheme: true,
  light: {
    syntax: '#555',
    ui: '#ddd',
    bg: '#eee'
  },
  dark: {
    syntax: '#ddd',
    ui: '#333',
    bg: '#555'
  }
};

export const ThemeContext = React.createContext<IThemeContext>(defaultState);

const ThemeContextProvider: React.FC = ({ children }) => {

  const [themeState, themeStateSet] = useState(defaultState)

  themeState.toggleTheme = () => {
    themeStateSet({ ...themeState, isLightTheme: !themeState.isLightTheme });
    console.log('toggled');
  };

  return (
    <ThemeContext.Provider value={themeState}>
      {children}
    </ThemeContext.Provider>
  );
}
export default ThemeContextProvider;