import React, { FC } from 'react';
import { config } from '../config';

// Highlights
const Timeline: FC = () => {
  return (
    <section className="colorlib-education" data-section="timelineedu">
      <div className="colorlib-narrow-content">
        <div className="row">
          <div className="col-md-6 col-md-offset-3 col-md-pull-3">
            <h2 className="colorlib-heading">{config.education_section.title}</h2>
          </div>

          <div className="accordion" id="accordionEdu" >

            {config.education_section.experiences.map((experience, index) => {
              const heading: string = `heading${index}`;
              const collapse: string = `collapse${index}`;
              return (
                <div className="accordion-item" key={index}>
                  <h2 className="accordion-header" id={heading}>
                    <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target={'#' + collapse} aria-expanded="false" aria-controls={collapse}>
                      <h2 style={{ textAlign: 'left' }}>{experience.name}<br></br><span>{experience.start_date} - {experience.end_date}</span>
                      </h2>
                    </button>

                  </h2>
                  <div id={collapse} className="accordion-collapse collapse" aria-labelledby={heading} data-bs-parent="#accordionEdu">
                    <div className="accordion-body">
                      <div className="row">
                        <div className={'col-sm-12' + (experience.highlights.length > 0 ? 'col-md-6 col-lg-6' : 'col-md-12 col-lg-12')}>
                          <p className="global-paragraph" dangerouslySetInnerHTML={{ __html: experience.description }}></p>
                        </div>
                        <div className={'col-sm-12' + (experience.highlights.length > 0 ? 'col-md-6 col-lg-6' : 'col-md-12 col-lg-12')}>
                          {experience.highlights_title ? (<h5 className="mb-1" style={{ marginLeft: '0.5rem' }}>{experience.highlights_title}</h5>) : null}
                          <ul>
                            {experience.highlights?.map((highlight, index) => (
                              <li key={index} dangerouslySetInnerHTML={{ __html: highlight.text }}></li>
                            ))}
                          </ul>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              )
            })}

          </div>
        </div>
      </div>
    </section >
  )

}
export default Timeline
