import React, { FC } from 'react';
import { config } from '../config';

const About: FC = () => {
  return (
    <section className="colorlib-about" data-section="about">
      <div className="colorlib-narrow-content">
        <div className="row">
          <div className="col-md-12">
            <div className="row row-bottom-padded-sm">
              <div className="about-desc">
                <h2 className="colorlib-heading">{config.about_section.title}</h2>
                <span className="heading-meta">{config.about_section.work_title}</span>
                <p className="global-paragraph">
                  {config.about_section.primary_text}
                </p>
                <p className="global-paragraph">
                  {config.about_section.secondary_text}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
export default About
