import React, { FC } from 'react';
import { config } from '../config';

const Timeline: FC = () => {

  return (
    <section className="colorlib-work" data-section="timelinework">
      <div className="colorlib-narrow-content">
        <div className="row">
          <div className="col-md-6 col-md-offset-3 col-md-pull-3">
            <h2 className="colorlib-heading">Work Experience</h2>
          </div>
        </div>
        <div className="row">
          <div className="col-md-12">
            <div className="timeline-centered">
              {config.experience_section.experiences.map((experience) => (
                <article className="timeline-entry">
                  <div className="timeline-entry-inner">
                    <div className="timeline-label">
                      <h2 className="mb-0">{experience.job_title} {experience.name === 'Freelance' ? '-' : 'at'} {experience.name}</h2>
                      <span>{experience.start_date} - {experience.end_date}</span>

                      {experience.short_description ? (
                        <p className="global-paragraph pt-2 mb-0">{experience.short_description}</p>)
                        : null
                      }
                      {experience.highlights.length > 0 ?
                        <ul className="mb-0 pt-2" >
                          {experience.highlights.map((highlight, index) => (
                            <li className="global-paragraph" key={index} dangerouslySetInnerHTML={{ __html: highlight.text }}>
                            </li>
                          ))}
                        </ul>
                        : null
                      }
                    </div>
                  </div>
                </article>
              ))}
            </div>
          </div>
        </div>
      </div>
    </section >

  )
}
export default Timeline
